export default {
	name: "ygcComment",
	//属性
	props: {
		placeholder: {
			type: String
		},
		maskState:{
			type:Number
		}
	},
	data() {
		return {
			content: '',
			focus: false
		};
	},
	created() {
	},
	methods: {
		stopPrevent(){
		},
		toggleMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			// this.maskState = 2;
			setTimeout(()=>{
				this.maskState = state;







				this.focus = this.maskState ? true : false;

			}, timer)
		},
		pubComment() {
			this.$emit('pubComment',this.content);
		},
		cancelComment(){
			this.$emit('cancelComment')
		}
	}
}
